.slider__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.slider__before {
  position: relative;
  clip-path: polygon(0 0, 50% 0, 50% 100%, 0 100%);
}

.slider__after {
  position: absolute;
  z-index: 1;
  height: 100%;
  clip-path: polygon(50% 0, 100% 0, 100% 100%, 50% 100%);
}

.slider__range {
  position: absolute;
  left: calc(50% - 2px);
  z-index: 2;
  width: 4px;
  height: 100%;
  background-color: #666666;
  border-radius: 2px;
}
.slider__range:hover {
  background-color: #68b738;
  cursor: pointer;
}
.slider__range:hover .slider__range-button {
  box-shadow: inset 0 0 0 2px #68b738;
  cursor: pointer;
}
.slider__range:hover .slider__range-button::before,
.slider__range:hover .slider__range-button::after {
  border-left-color: #68b738;
  cursor: pointer;
}
.slider__range:active {
  background-color: rgba(94, 170, 47, 0.6);
}
.slider__range:active .slider__range-button {
  box-shadow: inset 0 0 0 2px rgba(94, 170, 47, 0.6);
}
.slider__range:active .slider__range-button::before,
.slider__range:active .slider__range-button::after {
  border-left-color: rgba(94, 170, 47, 0.6);
}

.slider__range-button {
  position: absolute;
  top: calc(50% - 20px);
  left: -18px;
  width: 40px;
  height: 40px;
  background-color: #ffffff;
  border: none;
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px #666666;
}
.slider__range-button::before, .slider__range-button::after {
  content: "";
  position: absolute;
  top: 11px;
  left: 8px;
  width: 0;
  height: 0;
  background-color: #00000000;
  border-top: 9px solid transparent;
  border-bottom: 9px solid transparent;
  border-left: 8px solid #666666;
}
.slider__range-button::before {
  transform: rotate(180deg);
}
.slider__range-button::after {
  left: calc(50% + 4px);
}

